import React, { useState } from 'react';
import './About.css';

function About({ name, bio }) {
  // Example of using state to show/hide additional info
  const [showMore, setShowMore] = useState(false);

  return (
    <div className="about">
      <h2>About {name}</h2>
      <p>{bio}</p>
      {showMore && (
        <div className="extra-info">
          <p>
            I also enjoy working on open-source projects, collaborating with other developers, 
            and exploring new technologies to stay current in the fast-paced tech industry.
          </p>
        </div>
      )}
      <button onClick={() => setShowMore(!showMore)}>
        {showMore ? 'Show Less' : 'Read More'}
      </button>
    </div>
  );
}

export default About;
